class WGS84 {
  constructor(majorRadius, excentricity, flattening) {
    this.a = majorRadius;
    this.e = excentricity;
    this.f = flattening;
    this.k0 = 0.9996;

    this.ePow2 = Math.pow(this.e, 2);
    this.ePow4 = Math.pow(this.e, 4);
    this.ePow6 = Math.pow(this.e, 6);


    this.e1sq = this.ePow2 / (1 - this.ePow2);
  }

  static fromFlattening(majorRadius, flattening) {
    const e = Math.sqrt(2 * flattening - Math.pow(flattening, 2));

    return new this(majorRadius, e, flattening);
  }
}

export default WGS84.fromFlattening(6378137, 1 / 298.257223563);



