import AngleConverter from '../math/AngleConverter';
import WGS84 from './WGS84';

class UtmGridZone {
  constructor(gps) {
    this.k = WGS84.k0; // Point scale
    this.E = 500000;

    const long = AngleConverter.toDegree(gps.long);

    if (long < 0) {
      this.zone = Math.floor((180 + long) / 6 + 1);
    } else {
      this.zone = Math.floor(long / 6 + 31);
    }

    // zone 30: long0=-3° AND 6° by zone
    this.long = AngleConverter.toRadian(-3 + (this.zone - 30) * 6);

    this.N = gps.lat < 0 ? 10000000 : 0;
    this.hemi = this.N === 0 ? 1 : -1;
  }
}

export default UtmGridZone;



