import WGS84 from './WGS84';

// http://www.ibm.com/developerworks/library/j-coordconvert/#listing8
class UtmToGpsVariables {
  constructor(utm) {
    let northing = utm.N;
    const northernHemisphere = utm.hemi === 1;

    if (!northernHemisphere) {
      northing = 10000000 - northing;
    }

    const arc = northing / WGS84.k0;
    const mu = arc /
      (WGS84.a *
        (1 - (WGS84.e ** 2) / 4.0 - 3 * (WGS84.e ** 4) / 64.0 - 5 * (WGS84.e ** 6) / 256.0)
      );

    const ei = (1 - ((1 - WGS84.ePow2) ** (1 / 2.0))) / (1 + ((1 - WGS84.ePow2) ** (1 / 2.0)));

    const ca = 3 * ei / 2 - 27 * (ei ** 3) / 32.0;

    const cb = 21 * (ei ** 2) / 16 - 55 * (ei ** 4) / 32;
    const cc = 151 * (ei ** 3) / 96;
    const cd = 1097 * (ei ** 4) / 512;
    const phi1 = mu
      + ca * Math.sin(2 * mu)
      + cb * Math.sin(4 * mu)
      + cc * Math.sin(6 * mu)
      + cd * Math.sin(8 * mu);

    const n0 = WGS84.a / ((1 - (WGS84.e * Math.sin(phi1)) ** 2) ** (1 / 2.0));

    const r0 = WGS84.a * (1 - WGS84.ePow2) / (1 - (WGS84.e * Math.sin(phi1)) ** 2) ** (3 / 2.0);
    const fact1 = n0 * Math.tan(phi1) / r0;

    const _a1 = 500000 - utm.E;
    const dd0 = _a1 / (n0 * WGS84.k0);
    const fact2 = dd0 * dd0 / 2;

    const t0 = Math.tan(phi1) ** 2;
    const Q0 = WGS84.e1sq * Math.cos(phi1) ** 2;
    const fact3 = (5 + 3 * t0 + 10 * Q0 - 4 * Q0 * Q0 - 9 * WGS84.e1sq) * dd0 ** 4 / 24;

    const fact4 =
      (61 + 90 * t0 + 298 * Q0 + 45 * t0 * t0 - 252 * WGS84.e1sq - 3 * Q0 * Q0) * dd0 ** 6 / 720;

    const lof1 = _a1 / (n0 * WGS84.k0);
    const lof2 = (1 + 2 * t0 + Q0) * dd0 ** 3 / 6.0;
    const lof3 =
      (5 - 2 * Q0 + 28 * t0 - 3 * Q0 ** 2 + 8 * WGS84.e1sq + 24 * t0 ** 2) * dd0 ** 5 / 120;
    const _a2 = (lof1 - lof2 + lof3) / Math.cos(phi1);
    const _a3 = _a2 * 180 / Math.PI;

    this.latitude = 180 * (phi1 - fact1 * (fact2 + fact3 + fact4)) / Math.PI;

    if (!northernHemisphere) { this.latitude = -this.latitude; }

    const l0 = utm.zone > 0 ? 6 * utm.zone - 183.0 : 3.0;

    this.longitude = l0 - _a3;
  }
}


export default UtmToGpsVariables;



