import GpsToUtmVariables from './utm/GpsToUtmVariables';
import UtmToGpsVariables from './utm/UtmToGpsVariables';
import Utm from './utm/Utm';
import Gps from './utm/Gps';

class GpsToUtmConverter {
  static toUtm(gps, utmGridZone) {
    const variables = new GpsToUtmVariables(gps, utmGridZone);

    return new Utm(
      variables.E, variables.N, gps.alt, variables.utmGridZone.hemi, variables.utmGridZone.zone,
    );
  }

  static toGps(utm) {
    const variables = new UtmToGpsVariables(utm);

    return Gps.fromDegree(variables.latitude, variables.longitude, utm.alt);
  }
}

export default GpsToUtmConverter;



