class Matrix {
  /**
     *
     * @param n Row count
     * @param m Column count
     * @param values
     */
  constructor(n, m, values = null) {
    this.n = n;
    this.m = m;

    if (values === null) {
      this.values = new Array(this.n * this.m);
      this.values.fill(0);
    } else {
      this.values = values;
    }
  }

  get(i, j) {
    return this.values[this.getIndex(i, j)];
  }

  set(i, j, value) {
    this.values[this.getIndex(i, j)] = value;

    return this;
  }

  getIndex(i, j) {
    if (typeof i !== 'number' || i >= this.n) {
      throw new Error('Invalid row index');
    }
    if (typeof j !== 'number' || j >= this.m) {
      throw new Error('Invalid column index');
    }

    return i * this.m + j;
  }

  transpose() {
    const transpose = new Matrix(this.m, this.n);

    for (let i = 0; i < this.n; i++) {
      for (let j = 0; j < this.m; j++) {
        transpose.set(j, i, this.get(i, j));
      }
    }

    return transpose;
  }

  multiply(s) {
    if (typeof s !== 'number') {
      throw new Error('Expected a number');
    }

    this.values = this.values.map(
      value => value * s,
    );

    return this;
  }

  extractColumn(j) {
    const C = new Matrix(this.n, 1);
    for (let i = 0; i < this.n; i++) {
      C.set(i, 0, this.get(i, j));
    }

    return C;
  }

  static sum(A, B) {
    if (A.m !== B.m || A.n !== B.n) {
      throw new Error('Invalid given matrices');
    }

    return new Matrix(
      A.n, A.m, A.values.map(
        (value, index) => value + B.values[index],
      ),
    );
  }

  static identity(dim) {
    const I = new Matrix(dim, dim);

    for (let i = 0; i < dim; i++) {
      I.set(i, i, 1);
    }

    return I;
  }

  static additiveInverse(M) {
    return new Matrix(
      M.n, M.m, M.values.map(
        value => -1 * value,
      ),
    );
  }

  /**
     * https://fr.wikipedia.org/wiki/Produit_matriciel
     * @param A
     * @param B
     * @return {Matrix}
     */
  static product(A, B) {
    if (!(A instanceof Matrix && B instanceof Matrix)) {
      throw new Error('Expected two Matrices');
    }

    if (A.m !== B.n) {
      throw new Error('Invalid given matrices');
    }

    const size = A.m;
    const R = new Matrix(A.n, B.m);

    for (let i = 0; i < R.n; i++) {
      for (let j = 0; j < R.m; j++) {
        let value = 0;
        for (let k = 0; k < size; k++) {
          value += A.get(i, k) * B.get(k, j);
        }
        R.set(i, j, value);
      }
    }

    return R;
  }

  /**
     * https://en.wikipedia.org/wiki/Invertible_matrix#Methods_of_matrix_inversion
     * @param M
     * @return {Matrix}
     */
  static inverseMatrix3(M) {
    if (M.n !== 3 || M.m !== 3) {
      throw new Error('Works only with Matrix 3');
    }

    const C0 = M.extractColumn(0);
    const C1 = M.extractColumn(1);
    const C2 = M.extractColumn(2);

    const det = this.determinantMatrix3(M);

    if (det === 0) {
      throw new Error('Not inversible matrix');
    }

    const R0 = this.crossProductVector3(C1, C2).transpose().multiply(1 / det);
    const R1 = this.crossProductVector3(C2, C0).transpose().multiply(1 / det);
    const R2 = this.crossProductVector3(C0, C1).transpose().multiply(1 / det);

    const N = new Matrix(3, 3);

    N.set(0, 0, R0.get(0, 0));
    N.set(0, 1, R0.get(0, 1));
    N.set(0, 2, R0.get(0, 2));
    N.set(1, 0, R1.get(0, 0));
    N.set(1, 1, R1.get(0, 1));
    N.set(1, 2, R1.get(0, 2));
    N.set(2, 0, R2.get(0, 0));
    N.set(2, 1, R2.get(0, 1));
    N.set(2, 2, R2.get(0, 2));

    return N;
  }

  static determinantMatrix3(M) {
    if (M.n !== 3 || M.m !== 3) {
      throw new Error('Works only with Matrix 3');
    }

    const C0 = M.extractColumn(0);
    const C1 = M.extractColumn(1);
    const C2 = M.extractColumn(2);

    return Matrix.dotProductVector(C0, Matrix.crossProductVector3(C1, C2));
  }

  /**
     * https://en.wikipedia.org/wiki/Triple_product#Scalar_triple_product
     * @param U
     * @param V
     * @return {*}
     */
  static crossProductVector3(U, V) {
    if (U.n !== 3 || U.m !== 1 || V.n !== 3 || V.m !== 1) {
      throw new Error('Works only with Matrix 3x1');
    }

    const M = new Matrix(3, 1);

    M.set(0, 0, U.get(1, 0) * V.get(2, 0) - U.get(2, 0) * V.get(1, 0));
    M.set(1, 0, U.get(2, 0) * V.get(0, 0) - U.get(0, 0) * V.get(2, 0));
    M.set(2, 0, U.get(0, 0) * V.get(1, 0) - U.get(1, 0) * V.get(0, 0));

    return M;
  }

  /**
     * https://en.wikipedia.org/wiki/Dot_product
     * @param U
     * @param V
     * @return {Matrix}
     */
  static dotProductVector(U, V) {
    if (U.m !== 1 || V.m !== 1 || U.n !== V.n) {
      throw new Error('Works only with Matrix nx1');
    }

    let sum = 0;

    for (let i = 0; i < U.n; i++) {
      sum += U.get(i, 0) * V.get(i, 0);
    }

    return sum;
  }
}

export default Matrix;



