import AngleConverter from '../math/AngleConverter';

class Gps {
  constructor(latitude, longitude, altitude) {
    this.lat = latitude;
    this.long = longitude;
    this.alt = altitude;
  }

  static fromDegree(latitude, longitude, altitude) {
    return new this(
      AngleConverter.toRadian(latitude),
      AngleConverter.toRadian(longitude),
      altitude,
    );
  }

  get latInDegree() {
    return AngleConverter.toDegree(this.lat);
  }

  get longInDegree() {
    return AngleConverter.toDegree(this.long);
  }
}

export default Gps;



