import LinearSquareRegression from './math/LinearSquareRegression';
import Matrix from './math/Matrix';

class EuclideanSpaceTransform {
  constructor(transformMatrix, translateMatrix) {
    this.transformMatrix = transformMatrix;
    this.translateMatrix = translateMatrix;

    // this.a = a;
    // this.b = b;
    // this.c = c;
    // this.d = d;
    // this.fO = fO;
    // this.tO = tO;
    //
    // this.det = this.a * this.d - this.b * this.c;

    this._reverseTransform = null;
  }

  convert(position) {
    const u = new Matrix(3, 1, [position.x, position.y, position.z]);

    const v = Matrix.sum(Matrix.product(this.transformMatrix, u), this.translateMatrix);

    return { x: v.get(0, 0), y: v.get(1, 0), z: v.get(2, 0) };

    // return {
    //    x: this.a * (point.x - this.fO.x) + this.b * (point.y - this.fO.y) + this.tO.x,
    //    y: this.c * (point.x - this.fO.x) + this.d * (point.y - this.fO.y) + this.tO.y
    // }
  }

  revert(point) {
    return this.reverseTransform.convert(point);
  }

  get reverseTransform() {
    if (this._reverseTransform === null) {
      const reverseTransformMatrix = Matrix.inverseMatrix3(this.transformMatrix);

      const reverseTranslateMatrix = Matrix.additiveInverse(
        Matrix.product(reverseTransformMatrix, this.translateMatrix),
      );

      this._reverseTransform = new EuclideanSpaceTransform(
        reverseTransformMatrix, reverseTranslateMatrix,
      );
      this._reverseTransform._reverseTransform = this;
    }

    return this._reverseTransform;
  }

  getDistanceAverageError(fromPositions, toPositions) {
    const N = fromPositions.length;
    let distanceErrorSum = 0;
    for (let i = 0; i < N; i++) {
      const f = fromPositions[i];
      const t = toPositions[i];

      distanceErrorSum += this.constructor._getDistance(this.convert(f), t);
    }

    return distanceErrorSum / N;
  }

  getDistanceErrorRepartitionFromAverage(fromPositions, toPositions) {
    const N = fromPositions.length;
    const distanceAverage = this.getDistanceAverageError(fromPositions, toPositions);
    const distribution = new Array(10);
    distribution.fill(0);

    for (let i = 0; i < N; i++) {
      const f = fromPositions[i];
      const t = toPositions[i];
      const d = this.constructor._getDistance(this.convert(f), t);

      const distributionValue = Math.abs(distanceAverage - d) / distanceAverage * 100;
      let index = Math.trunc(distributionValue / 10);

      if (index > 9) {
        index = 9;
      }

      distribution[index]++;
    }

    return distribution.map(
      count => Math.floor(count / N * 100),
    );
  }

  getRelativeDistanceErrorFromAverage(fromPositions, toPositions) {
    const N = fromPositions.length;
    const distanceAverage = this.getDistanceAverageError(fromPositions, toPositions);
    const distribution = [];

    for (let i = 0; i < N; i++) {
      const f = fromPositions[i];
      const t = toPositions[i];
      const d = this.constructor._getDistance(this.convert(f), t);

      distribution[i] = Math.abs(distanceAverage - d) / distanceAverage * 100;
    }

    return distribution;
  }

  getAmplitudeError(fromPositions, toPositions) {
    const N = fromPositions.length;
    const min = {
      x: Infinity,
      y: Infinity,
      z: Infinity,
    };
    const max = {
      x: -Infinity,
      y: -Infinity,
      z: -Infinity,
    };

    for (let i = 0; i < N; i++) {
      const f = fromPositions[i];
      const t = toPositions[i];

      const r = this.convert(f);

      const x = Math.abs(r.x - t.x);
      const y = Math.abs(r.y - t.y);

      if (x < min.x) {
        min.x = x;
      }
      if (x > max.x) {
        max.x = x;
      }
      if (y < min.y) {
        min.y = y;
      }
      if (y > max.y) {
        max.y = y;
      }
    }

    return {
      x: max.x - min.x,
      y: max.y - min.y,
    };
  }

  static computeSample(fromPositions, toPositions) {
    return this._leastSquareComputeSample(fromPositions, toPositions);
  }

  static _leastSquareComputeSample(fromPositions, toPositions) {
    // TODO: identify incoherent sample due to user input error
    const results = [];

    for (let i = 0; i < fromPositions.length; i++) {
      const f = [...fromPositions];
      const t = [...toPositions];

      const fO = f[i];
      const tO = t[i];

      f[i] = f[0]; // eslint-disable-line prefer-destructuring
      t[i] = t[0]; // eslint-disable-line prefer-destructuring
      f[0] = fO;
      t[0] = tO;

      results.push(this._leastSquareComputeSampleWithOrigin(f, t));
    }

    return this._selectResultLeastAverageDistanceError(results, fromPositions, toPositions);
  }

  static _leastSquareComputeSampleWithOrigin(fromPositions, toPositions) {
    const measuresProjectionX = [];
    const measuresProjectionY = [];

    const f0 = fromPositions[0];
    const t0 = toPositions[0];
    for (let i = 1; i < fromPositions.length; i++) {
      const f = fromPositions[i];
      const t = toPositions[i];

      const fVector = new Matrix(2, 1);
      fVector.set(0, 0, f.x - f0.x);
      fVector.set(1, 0, f.y - f0.y);

      const tVector = new Matrix(2, 1);
      tVector.set(0, 0, t.x - t0.x);
      tVector.set(1, 0, t.y - t0.y);

      measuresProjectionX.push({ parameters: fVector, result: tVector.get(0, 0) });
      measuresProjectionY.push({ parameters: fVector, result: tVector.get(1, 0) });
    }

    const A = LinearSquareRegression.compute2ParametersRegression(measuresProjectionX);
    const B = LinearSquareRegression.compute2ParametersRegression(measuresProjectionY);

    const transformMatrix = new Matrix(3, 3);
    transformMatrix.set(0, 0, A.get(1, 0));
    transformMatrix.set(0, 1, A.get(2, 0));
    transformMatrix.set(0, 2, 0);
    transformMatrix.set(1, 0, B.get(1, 0));
    transformMatrix.set(1, 1, B.get(2, 0));
    transformMatrix.set(1, 2, 0);
    transformMatrix.set(2, 0, 0);
    transformMatrix.set(2, 1, 0);
    transformMatrix.set(2, 2, 1);

    const translateMatrix = new Matrix(3, 1);

    translateMatrix.set(0, 0, t0.x + A.get(0, 0) - A.get(1, 0) * f0.x - A.get(2, 0) * f0.y);
    translateMatrix.set(1, 0, t0.y + B.get(0, 0) - B.get(1, 0) * f0.x - B.get(2, 0) * f0.y);
    translateMatrix.set(2, 0, 0);

    return new EuclideanSpaceTransform(transformMatrix, translateMatrix);
  }

  static _linearComputeSample(fromPositions, toPositions) {
    const size = fromPositions.length;

    const results = [];
    for (let i = 0; i < size - 2; i++) {
      const fO = fromPositions[i];
      const tO = toPositions[i];

      for (let j = i + 1; j < size - 1; j++) {
        const fA = fromPositions[j];
        const tA = toPositions[j];

        for (let k = j + 1; k < size; k++) {
          const fB = fromPositions[k];
          const tB = toPositions[k];

          const r = this._linearComputeTripletSample(fO, fA, fB, tO, tA, tB);
          results.push(r);
        }
      }
    }

    return this._selectResultLeastAverageDistanceError(results, fromPositions, toPositions);
  }

  static _linearComputeTripletSample(fO, fA, fB, tO, tA, tB) {
    const fAO = {
      x: fA.x - fO.x,
      y: fA.y - fO.y,
    };
    const fBO = {
      x: fB.x - fO.x,
      y: fB.y - fO.y,
    };

    // Point vectors AO & BO in the to basis
    const tAO = {
      x: tA.x - tO.x,
      y: tA.y - tO.y,
    };
    const tBO = {
      x: tB.x - tO.x,
      y: tB.y - tO.y,
    };

    const b = (tBO.x * fAO.x - tAO.x * fBO.x) / (fBO.y * fAO.x - fBO.x * fAO.y);

    const d = (tBO.y * fAO.x - tAO.y * fBO.x) / (fBO.y * fAO.x - fBO.x * fAO.y);
    const a = (tAO.x - b * fAO.y) / fAO.x;
    const c = (tAO.y - d * fAO.y) / fAO.x;

    const transformMatrix = new Matrix(3, 3);
    transformMatrix.set(0, 0, a);
    transformMatrix.set(0, 1, b);
    transformMatrix.set(0, 2, 0);
    transformMatrix.set(1, 0, c);
    transformMatrix.set(1, 1, d);
    transformMatrix.set(1, 2, 0);
    transformMatrix.set(2, 0, 0);
    transformMatrix.set(2, 1, 0);
    transformMatrix.set(2, 2, 1);

    const translateMatrix = new Matrix(3, 1);
    translateMatrix.set(0, 0, tO.x - a * fO.x - b * fO.y);
    translateMatrix.set(1, 0, tO.y - c * fO.x - d * fO.y);
    translateMatrix.set(2, 0, 0);

    return new EuclideanSpaceTransform(transformMatrix, translateMatrix);
  }

  static _selectResultLeastAverageDistanceError(results, from, to) {
    let bestResult = null;
    let minDistanceAverageError = Infinity;

    if (!Array.isArray(results) || results.length === 0) {
      throw new Error('Expected a non empty array');
    }

    results.forEach((result) => {
      const distanceAverageError = result.getDistanceAverageError(from, to);

      if (distanceAverageError < minDistanceAverageError) {
        bestResult = result;
        minDistanceAverageError = distanceAverageError;
      }
    });

    return bestResult;
  }

  static _getDistance(p1, p2) {
    return Math.sqrt(Math.pow(p1.x - p2.x, 2) + Math.pow(p1.y - p2.y, 2));
  }
}

export default EuclideanSpaceTransform;



