import UtmGridZone from '../utm/UtmGridZone';
import WGS84 from './WGS84';

const S1 = 1 - WGS84.ePow2 / 4 - 3 / 64 * WGS84.ePow4 - 5 / 256 * WGS84.ePow6;
const S2 = 3 / 8 * WGS84.ePow2 + 3 / 32 * WGS84.ePow4 + 45 / 1024 * WGS84.ePow6;
const S3 = 15 / 256 * WGS84.ePow4 + 45 / 1024 * WGS84.ePow6;
const S4 = 35 / 3072 * WGS84.ePow6;

// https://fr.wikipedia.org/wiki/Transverse_universelle_de_Mercator
// http://www.ibm.com/developerworks/library/j-coordconvert/
class GpsToUtmVariables {
  constructor(gps, utmGridZone = null) {
    this.gps = gps;
    this.utmGridZone = utmGridZone || new UtmGridZone(this.gps);
    this.deltaLong = this.gps.long - this.utmGridZone.long;
    this.Nu = 1 / Math.sqrt(1 - WGS84.ePow2 * Math.pow(Math.sin(this.gps.lat), 2));

    this.A = this.deltaLong * Math.cos(this.gps.lat);
    this.APow2 = Math.pow(this.A, 2);
    this.APow3 = Math.pow(this.A, 3);
    this.APow4 = Math.pow(this.A, 4);
    this.APow5 = Math.pow(this.A, 5);
    this.APow6 = Math.pow(this.A, 6);

    const sin2Lat = Math.sin(2 * this.gps.lat);
    const sin4Lat = Math.sin(4 * this.gps.lat);
    const sin6Lat = Math.sin(6 * this.gps.lat);
    this.S = S1 * this.gps.lat - S2 * sin2Lat + S3 * sin4Lat - S4 * sin6Lat;

    this.T = Math.pow(Math.tan(this.gps.lat), 2);
    this.TPow2 = Math.pow(this.T, 2);

    this.C = WGS84.ePow2 / (1 - WGS84.ePow2) * Math.pow(Math.cos(this.gps.lat), 2);
    this.CPow2 = Math.pow(this.C, 2);

    this.E = this.A + (1 - this.T + this.C) / 6 * this.APow3;
    this.E += (5 - 18 * this.T + this.TPow2) * this.APow5 / 120;
    this.E *= this.utmGridZone.k * WGS84.a * this.Nu;
    this.E += this.utmGridZone.E;

    this.N = this.APow2 / 2;
    this.N += (5 - this.T + 9 * this.C + 4 * this.CPow2) * this.APow4 / 24;
    this.N += (61 - 58 * this.T + this.TPow2) * this.APow6 / 720;
    this.N *= this.Nu * Math.tan(this.gps.lat);
    this.N += this.S;
    this.N *= WGS84.a * this.utmGridZone.k;
    this.N += this.utmGridZone.N;
  }
}

export default GpsToUtmVariables;



