import Matrix from '../math/Matrix';

class LinearSquareRegression {
  /**
     *
     * https://fr.wikipedia.org/wiki/R%C3%A9gression_lin%C3%A9aire_multiple#Estimateur_des_moindres_carr.C3.A9s_ordinaires_.28EMCO.29
     * @param sample
     * @return {Matrix}
     */
  static compute2ParametersRegression(sample) {
    const N = sample.length;

    const F = new Matrix(N, 3);
    const T = new Matrix(N, 1);

    for (let j = 0; j < N; j++) {
      F.set(j, 0, 1);
      F.set(j, 1, sample[j].parameters.get(0, 0));
      F.set(j, 2, sample[j].parameters.get(1, 0));

      T.set(j, 0, sample[j].result);
    }

    const tF = F.transpose();

    return Matrix.product(Matrix.product(Matrix.inverseMatrix3(Matrix.product(tF, F)), tF), T);
  }
}

export default LinearSquareRegression;



